import React, { useState, useEffect } from 'react';
import TodoItem from './TodoItem';
import styled from 'styled-components'

const StyledList = styled.ul`
  padding-left: 0;
  li:not(:last-child){
    margin-bottom: 1.7vh;
  }
  &.view{
    background-color: #f1f1f1;
    border-radius: 8px;
    li{
      list-style:none;
    }
  }
`;

const DeleteListButton = styled.button`
  margin : 0.5rem 1rem;
  background: #bd2130c4;
  padding: 0.3rem 1rem;
  border-radius: 8px;
  border: none;
  color: ${props => props.textColor || "#FFFFFF"};
`;

const FlexEndWrapper = styled.div`
  display: flex;
  justify-content: flex-end;
`;

const TodoList = ({ listId, items, onItemCompleted, onDeleteItem, isSaved, onDeleteList }) => {

  return (
    <StyledList className={isSaved ? "view" : ""}>
      {items.map(item => (
        <TodoItem listId={listId} key={item.id} id={item.id} isSaved={isSaved} text={item.text} onItemCompleted={onItemCompleted} completed={item.done} onDeleteItem={onDeleteItem} />
      ))}
      {(isSaved && items.length) ?
        <FlexEndWrapper>
          <DeleteListButton onClick={onDeleteList}>Delete List</DeleteListButton>
        </FlexEndWrapper>
        :
        <></>
      }
    </StyledList>
  )
}

export default TodoList;

