import React, { useState, useEffect } from 'react';
import TodoList from './TodoList';
import styled from 'styled-components';
import ListActions from './ListActions';
import { FormControl } from '../CommonStyledComponents/FormControl';

const InputContainer = styled.div`
  display: flex;
  input{
    margin-right: 10px;
  }
  margin-bottom: 30px;
`;

const AddButton = styled.button`
    color: #fff;
    background-color: #007bff;
    border-color: #007bff;
    cursor: pointer;
    border: none;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: 0.25rem;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
`;

const ListCard = ({ listId, items, onSave, isSaved, onDeleteList, addItem, onDeleteItem, handleItemCompleted, onDeleteMultiple }) => {

  const [text, setText] = useState("");

  const onItemCompleted = (itemId) => {
    handleItemCompleted(listId, itemId)
  }

  const handleDeleteSelectedItems = (event) => {
    event.preventDefault();
    onDeleteMultiple(listId);
  }

  const handleSaveList = (event) => {
    event.preventDefault();
    onSave(listId, items);
  }

  const handleTextChange = (event) => {
    setText(event.target.value);
  }

  const handleDeleteList = (event) => {
    event.preventDefault();
    onDeleteList(listId);
  }

  const handleAddItem = (event) => {
    event.preventDefault();
    let newItem = {
      id: Date.now(),
      text: text,
      done: false
    };

    addItem(listId, newItem);
    setText('');
  }

  return (
    <>
      {!isSaved && <form>
        <InputContainer>
          <FormControl type="text" className="form-control" onChange={handleTextChange} value={text} />
          <AddButton onClick={handleAddItem} disabled={!text}>{"Add #" + (items.length + 1)}</AddButton>
        </InputContainer>
      </form>}

      <TodoList onDeleteList={handleDeleteList} listId={listId} isSaved={isSaved} items={items} onItemCompleted={onItemCompleted} onDeleteItem={onDeleteItem} />
      
      <ListActions viewOnly={isSaved} length={items.length} onDeleteList={handleDeleteList} onDeleteMultiple={handleDeleteSelectedItems} onSave={handleSaveList} />

    </>
  )
}

export default ListCard;