import React, { useState } from 'react';
import ListCard from './ListCard';
import styled from 'styled-components'

const Title = styled.h3`
  color: #3661ED;
`;

const TitleDiv = styled.div`
  justify-content: space-between;
  align-items: center;
  display: flex;
  margin-bottom: 1rem;
`;

const TabButton = styled.button`
  background-color: #1d9538;
  color: #ffffff;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  @media(min-width:961px){
    display: none;
  }
`;

const Card = styled.div`
  background: ${props => props.bgColor ? "#323435" : "#FFFFFF"};
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.01);
  border-radius: 34px;
  height: 75vh;
  display: flex;
  flex-direction: column;
  padding: 3vh 3vw;
`;

const LeftSide = styled(Card)`
  > ul {
    flex-grow: 1;
    overflow: auto;
  }
`;

const CardTitle = styled.h3`
  font-family: 'Inter';
  font-style: normal;
  font-weight: 600;
  color: ${props => props.isLight ? "#FFFFFF" : "#000000"};
  margin-bottom: 0;
`;

const SavedListsWrapper = styled.div`
  padding: 10px 30px 30px 30px;
  flex-grow: 1;
  overflow-y: auto;
`;

const Divider = styled.div`
  width: 100%;
  margin-top: 20px;  
  margin-bottom: 20px;  
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  border-color: ${props => props.color || "rgba(0, 0, 0, 0.1)"};
`;

const AppLayout = styled.div`
  display: flex;
  flex-direction: column;
  padding: 5vh 5vw;
  @media(min-width: 1440px){
    padding-right: 10vw;
    padding-left: 10vw;
  }
`;

const FlexBox = styled.div`
  display: flex;
  flex-grow: 1;
  @media (max-width:960px){
    flex-direction: column;
  }
  > div{
    flex-basis: 50%;
    @media (max-width:960px){
      &.hide-on-mobile{
        display: none;
      }
      flex-basis: 100%;
    }
  }
  > div:first-child{
    margin-right: 30px;
    @media (max-width:960px){
      margin-right: 0;
      margin-bottom: 3rem;
    }
  }
`;

const MainPage = () => {

  const [lists, setLists] = useState([{
    id: 'list' + Date.now(),
    isSaved: false,
    items: []
  }]);

  const [tab,setTab] = useState(true);

  const handleDeleteList = (listId) => {
    setLists(lists => lists.filter(list => list.id !== listId));
  }

  const handleAddItem = (listId, newItem) => {
    setLists(lists => lists.map(list => {
      if (list.id === listId) {
        let newListOfItems = [...list.items, newItem];
        return {
          ...list,
          items: newListOfItems
        }
      }

      return list;
    }))
  }

  const handleDeleteItem = (listId, itemId) => {
    setLists(lists => lists.map(list => {
      if (list.id === listId) {
        let newListOfItems = list.items.filter(item => item.id !== itemId);
        return {
          ...list,
          items: newListOfItems
        }
      }

      return list;
    }))
  }

  const handleItemCompleted = (listId, itemId) => {
    setLists(lists => lists.map(list => {
      if (list.id === listId) {
        let newItems = list.items.map(item => {
          if (item.id === itemId) {
            return { ...item, done: !item.done }
          }
          return item;
        })
        return {
          ...list,
          items: newItems
        }
      }

      return list;
    }))
  }

  const handleDeleteSelectedItems = (listId) => {
    setLists(lists => lists.map(list => {
      if (list.id === listId) {
        let newListOfItems = list.items.filter(item => !item.done);
        return {
          ...list,
          items: newListOfItems
        }
      }

      return list;
    }))
  }

  const saveList = (listId, newItems) => {
    let newEmptyList = {
      id: 'list' + Date.now(),
      isSaved: false,
      items: []
    };
    let newArray = [...lists, newEmptyList];
    setLists(lists => newArray.map(list => {
      if (list.id === listId) {
        return {
          ...list,
          items: newItems,
          isSaved: true
        }
      }

      return list;
    }))
  }

  return (
    <>
      <AppLayout>
        <TitleDiv>
          <Title>TO DO LIST</Title>
          <TabButton onClick={() => setTab(!tab)}>{tab ? "Saved List" : "New List"}</TabButton >
        </TitleDiv>
        <FlexBox>
          <div  className={!tab ? " hide-on-mobile ": ""}>
            <LeftSide>
              <CardTitle >New List</CardTitle>
              <Divider></Divider>

                {lists.filter(list => !list.isSaved).map(list => (
                  <ListCard key={list.id} listId={list.id} items={list.items} addItem={handleAddItem} onSave={saveList} isSaved={list.isSaved} onDeleteList={handleDeleteList}
                    onDeleteItem={handleDeleteItem}
                    handleItemCompleted={handleItemCompleted}
                    onDeleteMultiple={handleDeleteSelectedItems}
                  />
                ))}
            </LeftSide>
          </div>
          <div className={tab ? " hide-on-mobile ": ""}>
            <Card bgColor="#323435">
              <CardTitle isLight>Saved Lists</CardTitle>
              <Divider color='#afafaf'></Divider>

              <SavedListsWrapper>
                {
                  lists.filter(list => list.isSaved).map(list => (
                    <ListCard key={list.id} listId={list.id} items={list.items} addItem={handleAddItem} onSave={saveList} isSaved={list.isSaved} onDeleteList={handleDeleteList}
                      onDeleteItem={handleDeleteItem}
                      handleItemCompleted={handleItemCompleted}
                      onDeleteMultiple={handleDeleteSelectedItems}
                    />
                  ))
                }
              </SavedListsWrapper>
            </Card>
          </div>
        </FlexBox>
      </AppLayout>
    </>
  )
}

export default MainPage;