import React, { useState, useEffect } from 'react';
import styled from 'styled-components';

const ButtonsContainer = styled.div`
  display: flex;
  justify-content: flex-end;
  > div:not(:last-child){
    margin-right: 1rem;
  }
`;

const ActionButtons = styled.button`
  cursor: ${props => props.disabled ? "not-allowed" : "pointer"};
  background: #3661ED;
  border-radius: 8px;
  height: 55px;
  min-width: 160px;
  padding: 0 1rem;
  border: none;
  background-color: ${props => props.backgroundColor || "#3661ED"};
  color: ${props => props.textColor || "#FFFFFF"};
`;

const ListActions = ({ viewOnly, length, onDeleteList, onDeleteMultiple, onSave }) => {

  return (
    <>
      {viewOnly ?
        <>
          {/* <button onClick={onDeleteList} disabled={!length}>Delete List</button> */}
        </> :
        <ButtonsContainer>
          <div>
            <ActionButtons backgroundColor="#DCDCDC" textColor="black" onClick={onDeleteMultiple} disabled={!length}>Delete Selected Items</ActionButtons>
          </div>
          <div>
            <ActionButtons onClick={onSave} disabled={!length}>Save List</ActionButtons>
          </div>
        </ButtonsContainer>
      }
    </>
  )
}

export default ListActions;

